nav{
    background: rgb(252, 252, 252,0);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;
    margin-bottom: 80px;
    overflow: hidden;
    box-shadow: rgb(255, 255, 255,0.1) 3px 3px 10px;
}
.menu-link{
    font-size: 1.3rem;
    font-family: gluten;
   
}
.menu-link:hover{    text-shadow: rgb(255, 255, 255) 0.1rem 0.1rem 3px;
}
    
.menu{
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right:35px;

}
.menu li{
    margin-left: 10px;
    margin-right: 10px;  
    border-bottom: 3px solid transparent;
  
    position: relative;
    opacity:0;
}
.menu li:nth-child(1){
    -webkit-animation-name: drop;
            animation-name: drop;
    -webkit-animation-duration: 900ms;
            animation-duration: 900ms;
    -webkit-animation-delay: 200ms;
            animation-delay: 200ms;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.menu li:nth-child(2){
    -webkit-animation-name: drop;
            animation-name: drop;
    -webkit-animation-duration: 900ms;
            animation-duration: 900ms;
    -webkit-animation-delay: 400ms;
            animation-delay: 400ms;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}
.menu li:nth-child(3){
    -webkit-animation-name: drop;
            animation-name: drop;
    -webkit-animation-duration: 900ms;
            animation-duration: 900ms;
    -webkit-animation-delay: 600ms;
            animation-delay: 600ms;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.menu li:nth-child(4){
    -webkit-animation-name: drop;
            animation-name: drop;
    -webkit-animation-duration: 900ms;
            animation-duration: 900ms;
    -webkit-animation-delay: 800ms;
            animation-delay: 800ms;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.logo{
    position: relative;
    align-self: center;
    margin-left: 10px;
    font-family: logo;
    font-size: 2rem;
    text-shadow: rgb(80, 80, 80) 0.1rem 0.1rem 0;
    -webkit-animation: slidein;
            animation: slidein;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    color: white;
}
@media screen and (max-width:769px){
.bar{
    width: 25px;
    height:3px;
    display:block;
    background: white;
    margin:3px
}
.menu-toggle{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.bar.active:nth-child(1){
    -webkit-transform: translateY(2px) rotate(45deg);
            transform: translateY(2px) rotate(45deg);
    transition: all;
    transition-timing-function: ease;
    transition-duration: 1s;
}
.bar.active:nth-child(2){
    display: none;
    transition: all;
    transition-timing-function: ease;
    transition-duration: 1s;
}
.bar.active:last-child{
    -webkit-transform: translateY(-8px) rotate(-45deg);
            transform: translateY(-8px) rotate(-45deg);
    transition: all;
    transition-timing-function: ease;
    transition-duration: 1s;
}
.menu{
    display: none;
}
.menu.active{
    display: grid;
    grid-template-columns: auto;
    position: absolute;
    top:49px;
    right:-34px;
    height: 60vh;
    z-index: 6;
    background: linear-gradient(to right bottom, rgba(17, 0, 255, 0.2), rgba(7, 3, 255, 0.7));
}

.menu-link{
    margin: 20px;
}

}
@-webkit-keyframes slidein {
    from{
        -webkit-transform: translateX(-170px);
                transform: translateX(-170px);
    }
    to{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    
}
@keyframes slidein {
    from{
        -webkit-transform: translateX(-170px);
                transform: translateX(-170px);
    }
    to{
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
    
}
@-webkit-keyframes drop{
    from{
    
       -webkit-transform: translateY(-100px);
    
               transform: translateY(-100px);
    }
    to{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}
@keyframes drop{
    from{
    
       -webkit-transform: translateY(-100px);
    
               transform: translateY(-100px);
    }
    to{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}
