/* Animations */
@-webkit-keyframes slider{
    25%{
        top:-5px;
    }
    50%{
        top:-46px;
    }
    95%{
        top:-87px;
    }
}
@keyframes slider{
    25%{
        top:-5px;
    }
    50%{
        top:-46px;
    }
    95%{
        top:-87px;
    }
}
@-webkit-keyframes sliderTablet{
    25%{
        top:-5px;
    }
    50%{
        top:-40px;
    }
    95%{
        top:-74px;
    }
}
@keyframes sliderTablet{
    25%{
        top:-5px;
    }
    50%{
        top:-40px;
    }
    95%{
        top:-74px;
    }
}
@-webkit-keyframes dropin {
    from{
        -webkit-transform: scale(5);
                transform: scale(5);
        opacity: 0.2;
    }
    to{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
}
@keyframes dropin {
    from{
        -webkit-transform: scale(5);
                transform: scale(5);
        opacity: 0.2;
    }
    to{
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
}
@-webkit-keyframes float{
    from{
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    to{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        color: white;
    }
}
@keyframes float{
    from{
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    to{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        color: white;
    }
}
