.logos{
    width: 400px;
    height: auto;
    flex-basis: 40%;
    margin: 20px;
    transition: all ease 1s;
}
.logos:hover{
    transform: scale(1.2);
}
#illustrationContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 50px;
    margin-bottom: 100px;
}
@media screen and (max-width:376px){
    .logos{
        width: 350px;
    }
}
@media screen and (max-width:321px){
    .logos{
        width: 300px;
    }
}
@media screen and (max-width:300px){
    .logos{
        width: 280px;
    }
}