*{
    box-sizing: border-box;
    scroll-behavior: smooth;
}
a{
    text-decoration: none;
}
@import url('https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Gluten&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Gluten&display=swap');
.face{
    height: 450px;
    width: 450px;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: rgb(41, 41, 41) 3px 3px 20px;
    position: relative;
    animation: dropin 2s ease;
   margin: 0 auto;
}
.email{
    position: sticky;
    color: white;
    top: 130vh;
    transform:rotate(90deg) translateY(660px);
}
.fab:hover{
    color:rgb(10, 239, 255);
}
#welcome{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
}
.list-item:nth-child(1){
    color: tomato;
}
.list-item:nth-child(2){
    color: skyblue;
}
.list-item:last-child{
    color:thistle;
}
.container{
    width: 85%;
    margin: 0 auto;
 
}
.container1{
    width: 85%;
    margin: 0 auto;
    display: block;
}
.chevron{
    
    margin-top: 40px;
    color: rgb(255, 255, 255,0.3);
    animation: float;
    animation-duration: 1s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-fill-mode: forwards;
}
.fas{
    display: block;
    color: rgb(255, 255, 255,0.3);
    font-size: 1.9rem;
    cursor: pointer;
}
.text{
    font-size: 1.4rem;
    text-align:center;
}
.slider-wrapper{
    display: flex;
    justify-content: center;
    height: 30px;
    font-family: Architects Daughter;
    color: white;
    overflow:hidden;
}
#aboutme{
    width: 85%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 100px;
    margin-bottom: 100px;
    
}
#aboutmeImg{
    height: auto;
    width:80%;
}
.aboutmeTxt{
    padding: 0 40px 0 40px;
}
.aboutmePara{
    font-size: 1.1rem;
    line-height: 30px;
}


.list-item{
    margin: 5px;
    color: white;
}

.list{
    display: flex;
    flex-direction: column;
    position: relative;
    animation: slider;
    animation-duration: 9s;
    animation-timing-function:step-start;
    animation-iteration-count: infinite;
    animation-delay: 2s;
    top: -5px;
    text-align: left;
    
}
.welcomeTxt{
    font-size: 2.9rem;
    font-family: gluten;
    margin-top: 0;
}
.hide{
    display: none;
}
.image{
    height: auto;
    width: 100%;
  
    position: relative;
    top: -129px;
    left: -1px;
    opacity: 1;
}
.overlay{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    background: radial-gradient(rgba(61, 61, 61, 0.5), black);
    z-index: -2;
    opacity: 0.8;
    
    
}
.projectContainer{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.projectCard{
    transition-property: all;
    transition-timing-function: ease;
    transition-duration: 500ms;
    overflow: hidden;
    height: 500px;
    width: 400px;
    flex-grow: 0;
    padding-bottom: 40px;
    flex-shrink: 0;
    margin-bottom: 40px;
    margin-right: 20px;
    box-shadow: rgba(0, 0, 0, 0.3) 3px 3px 10px;
   
    background: linear-gradient(to right bottom, rgba(110, 110, 110, 0.55),rgba(116, 116, 116, 0.07));
}
.projectCard:hover{
    transform: translateY(-9px);
}
.projectContainer h3{
    text-align: center;
}

.projectImg:nth-child(2){
    background: url('./media/fake.png');
}
.projectTxt{
    padding: 0 25px 0 25px;
    font-size: 1rem;
}
