.buttons{
   margin: 0 auto;
   display: flex;
   justify-content: center;
}
.button{
    background: linear-gradient(rgba(255, 255, 255, 0.3),rgb(255, 255, 255,0.05));
    border-radius: 13px;
    width: 110px;
    padding-bottom: 20px;
    margin: 0 auto;
    position: relative;
    height: 40px;
    top: -20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 -4px rgb(0, 0, 0,0.7);
