@media screen and (max-width:769px){
    .text{
        font-size: 1.1rem;
        margin-top: 30px;
    }
    .list{
    animation: sliderTablet;
    animation-duration: 9s;
    animation-timing-function:step-start;
    animation-iteration-count: infinite;
    animation-delay: 2s;
    }
    .email{
    
        top: 130vh;
        transform:rotate(90deg) translateY(349px);
    }
    #aboutme{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
    }
    .aboutmeImg{
        margin: 0 auto;
        align-self: center;
    }
    #aboutmeImg{
        width: 50%;
        position: relative;
        
        left: 50%;
        transform: translateX(-50%);
    }
}
@media screen and (max-width:426px)
{
.face{
    height: 300px;
    width: 300px;
}
.email{
    font-size: 0.7rem;
    top: 90vh;
    transform:rotate(90deg) translateY(205px);
}
.image{
    top:-83px;
}
.slider-wrapper{
    font-size: 1rem;
    display: block;
   
    height: 85px;
}
.hide{
    display: inline;
}
.list{
    display: none;
}
.aboutmeTxt{
    padding: 0 25px 0 25px;
}
#aboutme{
    width: 90%;
}
.aboutmePara{
    font-size: 1rem;
}
.projectCard{
    transform: scale(1);
    margin-right: 0;
}
}
@media screen and (max-width:376px){
    .welcomeTxt{
        font-size: 2.1rem;
    }
    #welcome{
        height: 600px;
    }
    #aboutme{
        width: 98%;
    }
    .projectCard{
        transform: scale(0.85);
        margin-bottom: 0;
   
        
    }
    .email{
        top:90vh;
        transform: rotate(90deg),translate(0,179px);
    }
    .projectCard:hover{
        transform: scale(0.85),translateY(-10px);
    }
}
@media screen and (max-width:320px) {
    .projectCard{
        transform: scale(0.75);
        margin-bottom: 0;
        margin-right: auto;
        margin-left: auto;
        
    }
    .email{
        top:90vh;
        transform: rotate(90deg),translateY(151px);
    }
}

/* Animations */
@keyframes slider{
    25%{
        top:-5px;
    }
    50%{
        top:-46px;
    }
    95%{
        top:-87px;
    }
}
@keyframes sliderTablet{
    25%{
        top:-5px;
    }
    50%{
        top:-40px;
    }
    95%{
        top:-74px;
    }
}
@keyframes dropin {
    from{
        transform: scale(5);
        opacity: 0.2;
    }
    to{
        transform: scale(1);
        opacity: 1;
    }
}
@keyframes float{
    from{
        transform: translateY(-20px);
    }
    to{
        transform: translateY(0);
        color: white;
    }
}