@font-face{
    src: url('./media/Angle\ Janks.ttf');
    font-family:logo;
}
*{
    box-sizing: border-box;
    color: white;
}
@import url('https://fonts.googleapis.com/css2?family=Gluten&display=swap');
.logo, .menu-link{
    text-decoration: none;
      
}
#first{
    color: thistle;
}
#second{
    color: skyblue;
}
#third{
    color: tomato;
}
#fourth{
    color: rgb(84, 175, 89);
}
nav{
    background: rgb(252, 252, 252,0);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;
    margin-bottom: 80px;
    overflow: hidden;
    box-shadow: rgb(255, 255, 255,0.1) 3px 3px 10px;
}
.menu-link{
    font-size: 1.3rem;
    font-family: gluten;
   
}
.menu-link:hover{    text-shadow: rgb(255, 255, 255) 0.1rem 0.1rem 3px;
}
    
.menu{
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right:35px;

}
.menu li{
    margin-left: 10px;
    margin-right: 10px;  
    border-bottom: 3px solid transparent;
  
    position: relative;
    opacity:0;
}
.menu li:nth-child(1){
    animation-name: drop;
    animation-duration: 900ms;
    animation-delay: 200ms;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
}
.menu li:nth-child(2){
    animation-name: drop;
    animation-duration: 900ms;
    animation-delay: 400ms;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
    animation-iteration-count: 1;
}
.menu li:nth-child(3){
    animation-name: drop;
    animation-duration: 900ms;
    animation-delay: 600ms;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
}
.menu li:nth-child(4){
    animation-name: drop;
    animation-duration: 900ms;
    animation-delay: 800ms;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
}
.logo{
    position: relative;
    align-self: center;
    margin-left: 10px;
    font-family: logo;
    font-size: 2rem;
    text-shadow: rgb(80, 80, 80) 0.1rem 0.1rem 0;
    animation: slidein;
    animation-duration: 2s;
    animation-timing-function: ease;
    color: white;
}
@media screen and (max-width:769px){
.bar{
    width: 25px;
    height:3px;
    display:block;
    background: white;
    margin:3px
}
.menu-toggle{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.bar.active:nth-child(1){
    transform: translateY(2px) rotate(45deg);
    transition: all;
    transition-timing-function: ease;
    transition-duration: 1s;
}
.bar.active:nth-child(2){
    display: none;
    transition: all;
    transition-timing-function: ease;
    transition-duration: 1s;
}
.bar.active:last-child{
    transform: translateY(-8px) rotate(-45deg);
    transition: all;
    transition-timing-function: ease;
    transition-duration: 1s;
}
.menu{
    display: none;
}
.menu.active{
    display: grid;
    grid-template-columns: auto;
    position: absolute;
    top:49px;
    right:-34px;
    height: 60vh;
    z-index: 6;
    background: linear-gradient(to right bottom, rgba(17, 0, 255, 0.2), rgba(7, 3, 255, 0.7));
}

.menu-link{
    margin: 20px;
}

}
@keyframes slidein {
    from{
        transform: translateX(-170px);
    }
    to{
        transform: translateX(0);
    }
    
}
@keyframes drop{
    from{
    
       transform: translateY(-100px);
    }
    to{
        transform: translateY(0);
        opacity: 1;
    }
}